//GENERIC FOR ALL PAGES
.top_row {
  .hero {
    .slider {
      div.slick-slide:nth-of-type(1) {
        color: #000;
        .inner_slide {
          background: linear-gradient(to bottom, rgba(32, 124, 202, 0) 0%, rgba(16, 62, 101, 0) 50%, rgba(8, 30, 48, 0.3) 76%, rgba(0, 0, 0, 0.5) 100%), url('../img/hero-one.jpg') center center / cover no-repeat;
          @media only screen and (max-width: 750px) {
            background: linear-gradient(to bottom, rgba(32, 124, 202, 0) 0%, rgba(16, 62, 101, 0) 50%, rgba(8, 30, 48, 0.3) 76%, rgba(0, 0, 0, 0.5) 100%), url('../img/hero-one-mobile.jpg') center center / cover no-repeat;

          }
        }
      }
      div.slick-slide:nth-of-type(2) {
        color: #000;
        .inner_slide {
          background: linear-gradient(to bottom, rgba(32, 124, 202, 0) 0%, rgba(16, 62, 101, 0) 50%, rgba(8, 30, 48, 0.3) 76%, rgba(0, 0, 0, 0.5) 100%), url('../img/hero-two.jpg') center center / cover no-repeat;
          @media only screen and (max-width: 750px) {
            background: linear-gradient(to bottom, rgba(32, 124, 202, 0) 0%, rgba(16, 62, 101, 0) 50%, rgba(8, 30, 48, 0.3) 76%, rgba(0, 0, 0, 0.5) 100%), url('../img/hero-two-mobile.jpg') center center / cover no-repeat;

          }
        }
      }
    }
  }
}
.bottom_row {
  .sub-page {
    &:nth-child(1) {
      background: linear-gradient(to right, $brand-two 0%, $brand-two 100%);
    }
    &:nth-child(2) {
      background: linear-gradient(to right, $brand-two 0%, $brand-two 100%);
    }
    &:nth-child(3) {
      background: linear-gradient(to right, $brand-two 0%, $brand-two 100%);
    }
    &:nth-child(4) {
      background: linear-gradient(to right, $brand-two 0%, $brand-two 100%);
    }
    &:nth-child(5) {
      background: linear-gradient(to right, $brand-two 0%, $brand-two 100%);
    }
  }
}
