/* LABEL ======================================= */
.group.no-style {
  text-align: left;
}

.group.no-style label {
  position: relative;
  left: 0;
  top: 0;
}

label {
  color: #00369c;
  font-size: 14px;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  left: 5px;
  top: 10px;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

input.inputMaterial.not-required ~ label {
  left: 5px;
  top: 10px;
}

.not-empty, .inputMaterial:focus ~ label, .inputMaterial.not-required:focus ~ label, .inputMaterial:valid ~ label {
  top: -8px;
  font-size: 14px;
  color: #00369c;
}

.inputMaterial.optional ~ label {
  top: 10px;
}

/* BOTTOM BARS ================================= */
.bar {
  position: relative;
  display: block;
  width: 100%;
}

.bar:before, .bar:after {
  content: '';
  height: 2px;
  width: 0;
  bottom: 1px;
  position: absolute;
  background: #00369c;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

.bar:before {
  left: 50%;
}

.bar:after {
  right: 50%;
}

/* active state */
.inputMaterial:focus ~ .bar:before, .inputMaterial:focus ~ .bar:after {
  width: 50%;
}

/* active state */
.inputMaterial:focus ~ .highlight {
  -webkit-animation: inputHighlighter 0.3s ease;
  -moz-animation: inputHighlighter 0.3s ease;
  animation: inputHighlighter 0.3s ease;
}

/* ANIMATIONS ================ */
